    let close       = `/></svg>`;
   

    let likeNormal = [open, getViewBoxString(20,18), closeOpen, fill_1, closefill, likePath, close].join("");
    let likeActive = [open, getViewBoxString(20,18), closeOpen, fill_2, closefill, likePath, close].join("");

