    let upNormal    = [open, getViewBoxString(20,18), closeOpen, fill_1, closefill, upPath, close].join("");
    let upActive    = [open, getViewBoxString(20,18), closeOpen, fill_2, closefill, upPath, close].join("");
   
    let iconActive;
    let iconNormal;

    getIconType();
    
    $: current = iconNormal;


    const handleClick = () => {
        callback( type );
    }

    const handleMouseEnter = () => {
        current = iconActive;
    }

    const handleMouseLeave = () => {
        // if( !isActive) current = iconNormal;
        current = iconNormal;
    }

</script>



<div 
    class={"icon "} 
    on:click={handleClick}
    on:mouseenter={handleMouseEnter}
    on:mouseleave={handleMouseLeave}>
    
    {@html current}
   
</div>


<style>

    .icon {
        display:inline-block;
        user-select:none;
        cursor:pointer;
    }

</style>
